.CV {
  h1 {
    margin: 0;
  }
  h2 {
    margin: 20px 0 0 0;
  }
  ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
    background-color: $graybackground;
    border-style: double;
    border-color: $mainColor;
    margin: 10px;
    color: $mainColor;
  }
  li {
    list-style: none;
  }
}

.competence {
  display: flex;
  flex-direction: column;
  max-width: 1240px;
  width: 100%;
  ul {
    width: 33%;

    h4 {
      margin: 0;
    }
  }
}

.complementaire {
  display: flex;
  flex-direction: column;
  max-width: 1240px;
  width: 100%;
  ul {
    width: 50%;
  }
  h4 {
    margin: 0;
  }
}

.gridcompetence,
.gridcomplementaire {
  display: flex;
  width: 100%;
}
