.Card {
  border-radius: 40px;
  overflow: hidden;
  height: 340px;
  max-width: 500px;
  width: 100%;
  opacity: 1;
}
.Card img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.Card .cardTitle {
  position: absolute;
  bottom: 0;
  max-width: 340px;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  text-decoration: none;
  color: white;
  padding-left: 10px;
  text-overflow: ellipsis;
}
.Card:hover .cardTitle {
  display: none;
}
.Card .cardbackground {
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(202, 22, 22, 0) 0%, rgba(0, 0, 0, 0.5019607843));
  position: absolute;
  top: 0;
}
.Card .cardbackground .CardMore {
  display: none;
  width: 100%;
  height: 100%;
  color: white;
  font-weight: bold;
  font-size: 12px;
  margin: 5px;
}
.Card .cardbackground .CardMore p {
  padding: 5px;
}
.Card .cardbackground:hover {
  background: linear-gradient(rgba(0, 0, 0, 0.699) 0%, rgba(20, 20, 20, 0.6901960784));
}
.Card .cardbackground:hover .CardMore {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 10px;
  animation: backprojet 500ms ease-in;
}

@keyframes backprojet {
  from {
    transform: translateX(550px);
    width: 100%;
    opacity: 0;
  }
  to {
    opacity: 1;
    transform: translateX(0px);
    width: 100%;
  }
}
.ContainerCard {
  width: 100%;
  height: 100%;
}

a {
  text-overflow: ellipsis;
  position: relative;
}

@media (max-width: 576px) {
  .Card {
    height: 255px;
  }
