.collapse_header {
  background-color: #d8995d;
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
  border-radius: 10px;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  color: white;
  align-items: center;
  padding: 0 10px;
}

.collapse_content {
  z-index: -1;
  background-color: #f6f6f6;
  position: relative;
  margin-bottom: 25px;
  border-radius: 5px;
  color: #d8995d;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
