}

.arrowDown {
  animation: ArrowDown 300ms ease-in;
}

.arrowUp {
  animation: ArrowUp 300ms ease-in;
}

@keyframes ArrowDown {
  0% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(0deg);
    animation-fill-mode: forwards;
  }
}
@keyframes ArrowUp {
  0% {
    transform: rotate(-180deg);
  }
  100% {
    transform: rotate(0deg);
    animation-fill-mode: forwards;
  }
}
@keyframes slide {
  0% {
    transform: translateY(-60px);
    margin-bottom: 0px;
  }
  100% {
    transform: translateY(0px);
    margin-bottom: 25px;
  }
}
@media (max-width: 576px) {
  .collapse {
