  }
  .collapse_header {
    font-size: 13px;
    max-height: 30px;
  }
  .collapse_content {
    font-size: 12px;
  }
  @keyframes slide {
    0% {
      transform: translateY(-30px);
      margin-bottom: 0px;
    }
    100% {
      transform: translateY(0px);
      margin-bottom: 15px;
    }
  }
}
