.Card {
  border-radius: 40px;
  overflow: hidden;
  height: 340px;
  max-width: 500px;
  width: 100%;
  opacity: 1;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .cardTitle {
    position: absolute;
    bottom: 0;
    max-width: 340px;
    font-family: "Montserrat", sans-serif;
    font-size: 18px;
    text-decoration: none;
    color: $whiteColor;
    padding-left: 10px;
    text-overflow: ellipsis;
  }
  &:hover .cardTitle {
    display: none;
  }

  .cardbackground {
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(202, 22, 22, 0) 0%, #00000080);
    position: absolute;
    top: 0;

    .CardMore {
      display: none;
      width: 100%;
      height: 100%;
      color: $whiteColor;
      font-weight: bold;
      font-size: 12px;

      margin: 5px;
      p {
        padding: 5px;
      }
    }

    &:hover {
      background: linear-gradient(rgba(0, 0, 0, 0.699) 0%, #141414b0);
    }
    &:hover .CardMore {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      margin-top: 10px;
      animation: backprojet 500ms ease-in;
    }
  }
}

@keyframes backprojet {
  from {
    transform: translateX(550px);

    width: 100%;
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: translateX(0px);

    width: 100%;
  }
}

.ContainerCard {
  width: 100%;
  height: 100%;
}

a {
  text-overflow: ellipsis;
  position: relative;
}

@media (max-width: 576px) {
  .Card {
    height: 255px;
  }
  .cardTitle {
    padding: 0 10px;
  }
}
