.difficultyContainer {
  width: 1240px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  img {
    width: 32px;
    height: 32px;
    padding: 3px;
  }
}

.descProject {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.linkgit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 5px;
  padding-left: 0;
}

// Design page

.projetContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1240px;

  border-style: double;
  border-radius: 25px;
  background-color: $transmainColor;
}

.sliderContainer,
.langageContainer {
  width: 60%;
  max-width: 1240px;
  height: 200px;

  margin: 10px;
  border-style: double;
  border-radius: 25px;
  background-color: $transgraybackground;
}

.langageContainer {
  color: $mainColor;
  .imgLangage {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

    img {
      width: 100px;
    }
  }
}
.titleContainer,
.difficultyContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 30%;
  max-width: 1240px;

  margin: 10px;
  border-style: double;
  border-radius: 25px;
  background-color: $transgraybackground;
}
.difficultyContainer {
  color: $mainColor;
  font-size: 20px;
}
.line {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
}

.descriptionContainer,
.repoContainer {
  display: flex;
  justify-content: center;
  width: 95%;

  margin: 10px;
  border-style: double;
  border-radius: 25px;

  background-color: $transgraybackground;
  font-size: 25px;
  color: $mainColor;
  padding: 20px 0;
}

.titleContainer h1 {
  font-size: 50px;
  color: $mainColor;
  font-style: oblique;
}

.linksGit {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.NoLink {
  display: none;
}
