.gallery {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
  background-color: $transgraybackground;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 1240px;
  border-radius: 25px;
  gap: 50px;
  margin: auto;
  margin-bottom: 50px;
  padding: 50px 0px;
}
@keyframes projet {
  from {
    transform: scale(0);

    width: 100%;
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: scale(1);

    width: 100%;
  }
}

//BOUCLES QUI PERMETS D'AJOUTER DES PROJETS
@for $i from 0 through 15 {
  #animation_#{$i} {
    animation-duration: 1000ms;
    animation-name: projet;
    animation-fill-mode: forwards;
    opacity: 0;
    animation-delay: #{$i * 0.8}s;
  }
}
.presentation {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $transgraybackground;
  flex-direction: column;
  flex-wrap: wrap;
  max-width: 1240px;
  border-radius: 25px;
  gap: 10px;
  margin: auto;
  margin-bottom: 50px;
  padding: 30px 0px;

  .moi {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    border-left: darkgray;
    border-style: solid;
    border-right: none;
    border-top: none;
    border-bottom: none;

    p {
      display: flex;
      justify-content: center;
      margin: 0;
      text-align: center;
      padding: 0px 15px;
    }
    a {
      //padding-left: 5px;
      display: flex;
      justify-content: center;
      font-weight: bold;
      color: black;
      text-align: center;
      padding: 0px 15px;
    }
  }
  img {
    height: 100px;
    border-radius: 200px;
    margin-right: 20px;
  }
}
.gridPresentation {
  display: flex;
  align-items: center;
}
@media (max-width: 1024px) {
  .gallery {
    margin: 0 30px;
  }
  .presentation {
    margin: 0 30px;
    margin-bottom: 50px;

    .moi {
      display: flex;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      height: 100%;
      margin: auto;

      p {
        display: flex;
        justify-content: center;
        margin: 0;
        font-size: 12px;
      }
      a {
        //padding-left: 5px;
        font-weight: bold;
        color: black;
        font-size: 12px;
      }
    }
  }
}

@media (max-width: 576px) {
  .gallery {
    flex-direction: column;
    background-color: $whiteColor;
    margin: 0px 20px;
    padding: 10px 0px;
  }
  .presentation {
    flex-direction: column;

    margin: 0px 20px;
    padding: 10px 0px;

    .moi {
      display: flex;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      height: 100%;
      margin: auto;

      p {
        display: flex;
        justify-content: center;
        margin: 0;
        font-size: 12px;
      }
      a {
        //padding-left: 5px;
        font-weight: bold;
        color: black;
        font-size: 12px;
      }
    }
  }
}
