.centerError {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px;
  color: rgba(250, 59, 59, 0.699);
  border-style: inset;
  border-color: $transgraybackground;
  border-radius: 25px;
  background-color: rgba(0, 0, 0, 0.226);
}

.galleryerror {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
  background-color: $transgraybackground;
  flex-direction: row;
  flex-wrap: nowrap;
  max-width: 1240px;
  border-radius: 25px;
  gap: 10px;
  margin: auto;
  margin-bottom: 50px;
  padding: 20px 0px;
  height: 200px;

  .Card {
    width: 25%;
    margin: 10px;
    height: 100%;

    .CardMore {
      font-size: 10px;
    }
  }
}
